export const FILTERS = [
    { label: '', name: 'label' },
    { label: '', name: 'model' },
    { label: '', name: 'year' },
    { label: '', name: 'series' },
    { label: '', name: 'engine' },
    { label: '', name: 'modification' },
]

export const FILTER_NAMES = FILTERS.map(({ name }) => name)


